.card-container {
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    cursor: pointer;
    border: 1px solid rgb(224, 205, 205);
}

.button {
    justify-content: center;
}

.button:hover {
    background-color: rgb(205, 89, 89);
    border-color: rgb(205, 89, 89);
}

.title {
    color: black;
}

.rating {
    font-size: 1.2rem;
    color: gold;
    margin: 0.5rem;
}